import { Component, OnInit, HostBinding, ViewChild, TemplateRef } from '@angular/core';
import { GalleryComponent, Gallery, GalleryItem ,GalleryRef } from '@ngx-gallery/core';
import { Lightbox } from '@ngx-gallery/lightbox';
import { ActivatedRoute } from '@angular/router';
import { HostListener } from "@angular/core";

import { Photos } from '../photos'
import { Photo } from '../photo'


import { IMAGEVIEWER_CONFIG, ImageViewerConfig, createButtonConfig } from '@hallysonh/ngx-imageviewer';

const MY_IMAGEVIEWER_CONFIG: ImageViewerConfig = {
  bgStyle: 'transparent', // custom container's background style
  loadingMessage: 'Loading ...',
  rotateRightButton: createButtonConfig('','',4,false),
  rotateLeftButton: createButtonConfig('','',4,false),
};

@Component({
  selector: 'app-photo-detail',
  templateUrl: './photo-detail.component.html',
  styleUrls: ['./photo-detail.component.css'],
  providers: [
    {
      provide: IMAGEVIEWER_CONFIG,
      useValue: MY_IMAGEVIEWER_CONFIG,
    }
  ],
})

export class PhotoDetailComponent implements OnInit {
  screenHeight: number;
  screenWidth: number;

  galleryId ='gal';
  name: string;
  photos: Photo[];
  items: GalleryItem[];
  galRef: GalleryRef;
  lightbox: Lightbox;

  @ViewChild('itemTemplate', {static: true}) itemTemplate: TemplateRef<any>;

  constructor(
    gallery: Gallery,
    private route: ActivatedRoute,
  ) {
    this.galRef = gallery.ref(this.galleryId);
  }

  ngOnInit() {
    this.getScreenSize();

    this.galRef.setConfig({
      thumb: false,
      loadingMode: 'indeterminate',
      itemTemplate: this.itemTemplate,
      gestures: false,
      //imageSize: 'cover',
      loadingIcon: 'Loading...'
    });

    this.route.data.subscribe((data: { photos: Photos }) => {
      this.photos = data.photos.imgs;
      this.name = data.photos.name;
      this.items = this.photos.map(p => { //format of galleryItem
        return{
          type:'image',
          data:{
            src: p.srcURL,
            thumb: p.preURL,
            title: p.title,
            description: p.description,
            date: p.date
          }
        };
      });
      this.galRef.load(this.items);
    });

  }

  @HostListener('window:resize', ['$event'])
  getScreenSize(event?) {
    this.screenHeight = window.innerHeight;
    this.screenWidth = window.innerWidth;
  }

}
